<template>
  <v-layout justify-center align-center class="ma-10">
    <v-flex md12 fill-height>
      <v-card width="100%" style="min-height: calc(100vh - 100px)">
        <v-card-title class="blue--text justify-center mt-10" style="font-size:14px">
          <strong>Market Place - Sustainable Products </strong>
          <div class="text-center">
          <v-dialog v-model="newdialog" width="500">
            <template v-slot:activator="{ on }">
            <v-btn class="primary text-right" dark v-on="on">+ New </v-btn>
            </template>
            <v-card>
            <v-card-title class="headline grey lighten-2" primary-title>
            Add New Item
            </v-card-title>
            <v-card-text>
            <v-form ref="newcform">
            <v-text-field v-model="title" :counter="10" label="Product Name" :rules="nameRules" required></v-text-field>
            <v-text-field v-model="target" label="Price" required></v-text-field>
             <v-menu
          ref="menu1"
          v-model="menu1"
          :close-on-content-click="false"
          transition="scale-transition"
          offset-y
          full-width
          max-width="290px"
          min-width="290px"
        >
             <template v-slot:activator="{ on }">
              <v-text-field
                v-model="dateFormatted"
                label="Date"
                hint="MM/DD/YYYY format"
                persistent-hint
                prepend-icon="mdi-watch"
                @blur="date = parseDate(dateFormatted)"
                v-on="on"
              ></v-text-field>
          </template>
          <v-date-picker v-model="date" no-title @input="menu1 = false"></v-date-picker>
          </v-menu>
            <v-file-input v-model="media" label="Select a Media/Image"  required></v-file-input>         
            </v-form>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
            <div class="flex-grow-1"></div>
            <v-btn color="primary" text @click="newdialog = false">Close</v-btn>
            </v-card-actions>
            </v-card>
            </v-dialog>
          </div>
       
        </v-card-title>
        <v-divider />
        <v-tabs v-model="tab" background-color="transparent" grow>
        <v-tab v-for="location in locations" :key="location.name" :href="'#' + location.name">{{location.name}}</v-tab>
        <v-tab-item v-for="location in locations" :key="location.name + 1" :value="location.name" class="ma-5">
  

        <template v-for="product in location.products"> 
          <v-row :key="product.title">   
            <v-col v-for="n in 2" :key="n" class="d-flex child-flex">
            <v-card class="mx-auto" max-width="300">
              <v-img contain height="200" :src="require(`../assets/${product.image}`)" :alt="product.description">
              <v-card-title class="align-end fill-height">{{product.price}}</v-card-title>
              </v-img> 
                <v-card-title>
                  <div>{{product.title}}</div>
                  <p class="orange--text subtitle-1">{{product.date}}</p>
                </v-card-title>

                <v-card-actions>
                  <v-btn text>Buy</v-btn>
                  <div class="flex-grow-1"></div>

                  <v-btn icon @click="show = !show"
                  >
                    <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                  </v-btn>
                </v-card-actions>

                <v-expand-transition>
                  <div v-show="show">
                    <v-card-text>
                     {{product.description}}
                    </v-card-text>
                  </div>
                </v-expand-transition>
              </v-card>            
            </v-col>
          </v-row>
        </template>
        </v-tab-item>
     </v-tabs>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  name: 'Marketplace',
  data () {
    return {
      msg: 'Marketplace',
      newdialog: false,
      show: false,
      locations: [
        {
          name: 'Cary',
          products: 
            [
             { id: 1, color: 'red', title: 'Reusable Coffee Cups', date: 'today', description: 'Similar style to coffee cups you get from Starbucks and other shops,also made to baristas standards, so they fit snuggly in the machine at your favourite coffee shop or at your office vending machine.' ,image: 'coffeecup.png', price: '19.98', quantity: 4},
             { id: 2, color: 'green', title: 'Wool Footwear', date: '5 days ago', description: 'An innovative wool fabric made specifically for footwear, gives you more comfort.', image: 'woolshoes.png', price: '10', quantity: 4},
             { id: 3, color: 'blue', title: 'Trash Bags', date: '10 days ago', description: 'Non Plastic Trash bas made to save our mother earth', image: 'trashbags.png', price: '10.99', quantity: 4}
           ]
        },
        {
          name: 'UK',
          products: 
            [
             { id: 1, color: 'red', title: 'Reusable Coffee Cups', date: 'today', description: 'Similar style to coffee cups you get from Starbucks and other shops,also made to baristas standards, so they fit snuggly in the machine at your favourite coffee shop or at your office vending machine.' ,image: 'uk-coffeecup.png', price: '19.98', quantity: 4},
             { id: 2, color: 'green', title: 'Wool Footwear', date: '5 days ago', description: 'An innovative wool fabric made specifically for footwear, gives you more comfort.', image: 'woolshoes.png', price: '10', quantity: 4},
             { id: 3, color: 'blue', title: 'Trash Bags', date: '10 days ago', description: 'Non Plastic Trash bas made to save our mother earth', image: 'trashbags.png', price: '10.99', quantity: 4}
           ]
        }
      ],
    }
  }
}
</script>

