<template>
  <v-layout justify-center align-center>
    <v-flex md12 fill-height>
      <v-card width="100%" style="min-height: calc(100vh - 100px)">
        <v-card-title class="justify-center" style="height:70px">
          <span style="font-size: 14px; text-align: center">Challenges</span>
        </v-card-title>
        <v-divider />
        <v-tabs v-model="tab" background-color="transparent" grow>
          <v-tab href="#newc">New</v-tab>
          <v-tab href="#viewc">Browse</v-tab>
          <v-tab-item value="newc" class="ma-5">
            <v-container>
              <p>Please fill all the required details below to start a new challenge:</p>
              <v-form ref="newcform">
                <v-text-field
                  v-model="title"
                  :counter="10"
                  label="Challenge Name"
                  :rules="nameRules"
                  required
                ></v-text-field>
                <v-select v-model="type" :items="items" label="Type" required></v-select>
                <v-text-field
                  v-model="target"
                  label="Select the list of people you wanted to challeng"
                  required
                ></v-text-field>
                <v-text-field v-model="target" label="Enter your target" required></v-text-field>
                <v-file-input v-model="media" label="Select a Media/Image" required></v-file-input>
                <div class="text-center pb-8">
                  <p
                    v-if="success"
                    class="green"
                  >A new challenge has been added and you can view from View tab.</p>
                  <v-btn class="primary mr-4" @click="addNewChallenge">Submit</v-btn>
                  <v-btn class="primary">Cancel</v-btn>
                </div>
              </v-form>
            </v-container>
          </v-tab-item>
          <v-tab-item value="viewc">
            <v-card class="mx-auto" min-width="100%">
              <v-card-title>Join a challenge</v-card-title>
              <v-container class="pa-2" fluid>
                <v-row>
                  <v-col v-for="card in cards" :key="card.title" :cols="card.flex">
                    <v-card>
                      <v-img
                        :src="card.src"
            
                        class="white--text"
                        height="200px"
                        gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                      >
                        <v-card-title class="fill-height align-end" v-text="card.title"></v-card-title>
                      </v-img>

                      <v-card-actions>
                        <div class="flex-grow-1"></div>

                        <v-btn icon>
                          <v-icon>mdi-heart</v-icon>
                        </v-btn>

                        <v-btn icon>
                          <v-icon>mdi-bookmark</v-icon>
                        </v-btn>

                        <v-btn icon>
                          <v-icon>mdi-share-variant</v-icon>
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-col>
                </v-row>
              </v-container>
            </v-card>
          </v-tab-item>
        </v-tabs>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  name: "Challenges",
  data: () => ({
    success: false,
    items: ["Personal", "Team"],
    challenges: [
      {
        title: "August Plastic Challenge",
        type: "Team",
        media: "no-plastic.png",
        description:
          "This challenge is reduce the plastic usage 50% among our team.",
        status: "Completed"
      },
      {
        title: "September Rideshare Challenge",
        type: "Personal",
        media: "pubtrans.png",
        description: "Will use public tranportation for whole month September.",
        status: "Completed"
      }
    ],
    nameRules: [v => v.length >= 3 || "Minmum Length is 3 Characters"],
    cards: [
      {
        title: "Upcycled Art",
        src: "https://blog.publicgoods.com/wp-content/uploads/2019/05/PublicGoods_ContentImagery_201901_RecyclingDowncyclingUpcycling_Featured_3200x1600-1.jpg",
        flex: 12
      },
      {
        title: "Trash Weigh-In",
        src: "https://elnidoenvironment.files.wordpress.com/2012/12/02-weighing-the-trash.jpg",
        flex: 6
      },
      {
        title: "Meatless Monday",
        src: "https://blog.busuu.com/wp-content/uploads/2019/02/how-to-say-vegan-vegetarian-different-languages-696x464.jpg",
        flex: 6
      }
    ]
  }),
  methods: {
    addNewChallenge: function() {
      if (this.$refs.newcform.validate()) {
        this.challenges.push({
          title: this.title,
          type: this.type,
          status: "New"
        });
        this.success = true;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container {
  font-size: 14px;
}
p,
input {
  font-size: 14px;
}
</style>